@keyframes rotate-minutes {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-seconds {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  z-index: 1;
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s linear rotate-minutes;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch__second-hand {
  z-index: 1;
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite rotate-seconds;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

